    resetColor(): void{

      this.myFormColor.reset();
      this.galleryImages = [];

    }

    varAccion : any;
    accionColor(accion, color): void{

      console.log(color);
      this.galleryImages = [];

      this.varAccion = accion;

      if (color == 0) {
        this.myFormColor.reset();
      }else{
        this.myFormColor.patchValue({id : color.id});
        this.myFormColor.patchValue({nombre_color : color.nombre_color});
        this.myFormColor.patchValue({codigo_color : color.codigo_color});

        for (var i = 0; i <  color.imagen.length; i++) {
          this.galleryImages.push(
             {
                small: color.imagen[i].src,
                medium: color.imagen[i].src,
                big: color.imagen[i].src
            }
           );
        }
      }

    }

    editarColor(): void{

      this.loading = true;

      var imagenes_color = [];
      for (var i = 0; i < this.galleryImages.length; i++) {
        imagenes_color.push({src:this.galleryImages[i].small});
      }

      var datos= {
          token: localStorage.getItem('luvas_token'),
          nombre_color: this.myFormColor.value.nombre_color,
          codigo_color: this.myFormColor.value.codigo_color,
          imagen: JSON.stringify(imagenes_color)
          
        }

        this.http.put(this.rutaService.getRutaApi()+'colores/'+this.myFormColor.value.id, datos)
           .toPromise()
           .then(
             data => { // Success
                console.log(data);
                this.data = data;

                for (var i = 0; i < this.colores.length; ++i) {
                  if (this.colores[i].id == this.myFormColor.value.id) {
                    this.colores[i].nombre_color = this.myFormColor.value.nombre_color;
                    this.colores[i].codigo_color = this.myFormColor.value.codigo_color;
                    this.colores[i].imagen = imagenes_color;
                  }
                }

                setTimeout(()=>{
                  this.resetColor();
                },100);

                this.loading = false;

                this.alerta_tipo = 'success';
                this.alerta_msg = this.data.message;
                this.alerta = true;
                setTimeout(()=>{this.alerta = false;},4000); 
             },
             msg => { // Error
               console.log(msg);
               console.log(msg.error.error);

               this.loading = false;

               this.resetColor();

               //token invalido/ausente o token expiro
               if(msg.status == 400 || msg.status == 401){ 
                    //alert(msg.error.error);
                    //ir a login

                    this.alerta_tipo = 'warning';
                    this.alerta_msg = msg.error.error;
                    this.alerta_boton = true;
                    this.mostrar = false;
                }
                else { 
                    //alert(msg.error.error);

                    this.alerta_tipo = 'danger';
                    this.alerta_msg = msg.error.error;
                    this.alerta = true;
                    setTimeout(()=>{this.alerta = false;},4000);
                }
             }
           );
      
    }

    removerImg(): void{
      if (this.galleryImages.length > 0) {
        this.galleryImages.splice(this.galleryImages.length - 1, 1);
      }
      
    }

